//components
import { NavLink } from "react-router-dom";
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import LogoutIcon from '@mui/icons-material/Logout';
import Button from "@material-ui/core/Button";

import { makeStyles } from "@material-ui/core/styles";

//dependencias
import useUser from '../../hooks/useUser'

const useStyles = makeStyles((theme) => ({
  link: {
    margin: theme.spacing(1, 1.5),
    textTransform:'capitalize'
  },
}));

/**
 * Navbar para el header
 * @returns Navbar para el header, dependiendo si esta logeado se retorna un componente diferente
 */
const Navbar = () => {
  const {isLogged, logout } = useUser()
  const classes = useStyles();

  /**
   * Función para ejecutar logout del hook useUser.
   * @param {*} e 
   */
  const handleClick = e => {
    e.preventDefault()
    logout()
  }

  return (      
      isLogged ?
      <>
      <Button
        href="#"
        variant="outlined"
        sx={{ color: '#ffff' }} 
        className={classes.link}
        component={NavLink}
        to="/Perfil"
      >
        <AccountCircleIcon fontSize="small"></AccountCircleIcon> 
        &nbsp; PERFIL
      </Button>
      <Button
        href="#"
        color="primary"
        variant="outlined"
        className={classes.link}
        component={NavLink}
        to='#' onClick={handleClick}
      >
        <div className="d-flex justify-content-center align-items-center">

        <LogoutIcon fontSize="small"></LogoutIcon>
        &nbsp; SALIR
        </div>
      </Button>
      </>
      :
      <>
      <Button
        href="#"
        color="primary"
        variant="outlined"
        className={classes.link}
        component={NavLink}
        to="/loginuser">
        <AccountCircleIcon fontSize="small"></AccountCircleIcon> 

        &nbsp; INICIAR SESIÓN
      </Button>
      </>    
    );
};

export default Navbar;
