import  { useEffect, useState, Fragment } from "react";
import Navbar from "./navbar/navbar";
import { NavLink } from "react-router-dom";
import Switch from "../Switch"
import {  useContext } from "react";

//dependencies
import Context from "./context/UserContext";
import useUser from '../hooks/useUser'
import Sidebar from "./navbar/sidebarUser";
import { ListCategorias } from "../services/category";
//componentes
import {  Toolbar, Typography, AppBar,Box } from "@material-ui/core";
import Link from "@material-ui/core/Link";
import { makeStyles } from "@material-ui/core/styles";

import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css'


const useStyles = makeStyles((theme) => ({
  appBar: {
    borderBottom: `4px solid ${theme.palette.divider}`,
  },
  link: {
    margin: theme.spacing(1, 1.5),
  },
  toolbarTitle: {
    flexGrow: 1,
  },
  paper: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
}));

/**
 * Componente Header
 * @returns Componentes de header, si está logeado se envia sidebar
 */
const Header = () => {
  const classes = useStyles();
  const { isLogged } = useUser()

  const [open] = useState(false);
  const [categories, setCategories] = useState("");
  const { user } = useContext(Context)

  /**
   * Función para enviar la consulta de categorias al backend
   */
  const listCategorias = async () => {
    try {
      const res = await ListCategorias();
      setCategories(res);
    } catch (error) {
      console.log("Error");
    }
  };
  
  /**
   * hook para ejecutar la función listCategorias solo si está logeado.
   */
  useEffect(() => {
    if (isLogged) {
      listCategorias();
      
    }
  }, [isLogged]);

  if (isLogged && categories) {
    return <Sidebar user={user} categories={categories} />;
  } else {
    return (
      <Fragment>
        <Box>
          <AppBar
            position="relative"
            open={open}
            color="default"
            elevation={0}
            className={classes.appBar}
          >
            <Toolbar className={classes.toolbar}>
              <Typography
                variant="h6"
                color="inherit"
                noWrap
                className={classes.toolbarTitle}
              >
                <Link
                  component={NavLink}
                  to="/"
                  underline="none"
                  color="textPrimary"
                >
                  <LazyLoadImage effect="blur"  src={'https://sccotview.com/media/videos/logoSCCOTView.png'} alt="" className="" style={{ width:"170px", objectFit:'contain',minHeight:"2px" }} />
                 
                </Link>
              </Typography>
              <Switch></Switch> 
              <Navbar></Navbar>
            </Toolbar>
          </AppBar>
        </Box>
      </Fragment>
    );
  }
};

export default Header;
